___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    min-height: 100vh;
}


.container {
    margin-top: 2%;
    margin-left: 10%;
    width: 80%;
    min-height: 90vh;
    border: 1px solid black;
    display: grid;
    grid-template-rows: 1fr 10fr 1fr 10fr;
    grid-template-columns: 2fr 1fr;
    column-gap: 10px;
    align-items: center;
    justify-items: center;
}

.container > h3 {
    grid-column: 1 / 2;
}

.board {
    height: 90%;
    width: 90%;
    background-color: black;
    grid-column: 1 / 2;
    display: grid;
    grid-template: repeat(10, 1fr) / repeat(10, 1fr);
    gap: 2px;
}

#instructions {
    border: 1px solid black;
}


button:hover {
    cursor: pointer;
}

.cell {
    background-color: blue;
    border: none;
}

.cell:hover {
    border: 1px solid yellow;
}

.miss {
    background-color: white;
}

.hit {
    background-color: red;
}

dialog {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 5fr 1fr;
    align-items: center;
    justify-items: center;
}

#ship-to-add {
    border: 1px solid black;
}

.highlight {
    border: 2px solid yellow;
}

.active-ship {
    background-color: yellow;
}

.sunk-ship {
    background-color: orange;
}

.inactive-board {
    opacity: 0.2;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;;AAGA;IACI,iBAAiB;AACrB;;;AAGA;IACI,cAAc;IACd,gBAAgB;IAChB,UAAU;IACV,gBAAgB;IAChB,uBAAuB;IACvB,aAAa;IACb,qCAAqC;IACrC,8BAA8B;IAC9B,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,UAAU;IACV,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;IACb,gDAAgD;IAChD,QAAQ;AACZ;;AAEA;IACI,uBAAuB;AAC3B;;;AAGA;IACI,eAAe;AACnB;;AAEA;IACI,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,YAAY;AAChB","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n\nbody {\n    min-height: 100vh;\n}\n\n\n.container {\n    margin-top: 2%;\n    margin-left: 10%;\n    width: 80%;\n    min-height: 90vh;\n    border: 1px solid black;\n    display: grid;\n    grid-template-rows: 1fr 10fr 1fr 10fr;\n    grid-template-columns: 2fr 1fr;\n    column-gap: 10px;\n    align-items: center;\n    justify-items: center;\n}\n\n.container > h3 {\n    grid-column: 1 / 2;\n}\n\n.board {\n    height: 90%;\n    width: 90%;\n    background-color: black;\n    grid-column: 1 / 2;\n    display: grid;\n    grid-template: repeat(10, 1fr) / repeat(10, 1fr);\n    gap: 2px;\n}\n\n#instructions {\n    border: 1px solid black;\n}\n\n\nbutton:hover {\n    cursor: pointer;\n}\n\n.cell {\n    background-color: blue;\n    border: none;\n}\n\n.cell:hover {\n    border: 1px solid yellow;\n}\n\n.miss {\n    background-color: white;\n}\n\n.hit {\n    background-color: red;\n}\n\ndialog {\n    width: 100%;\n    height: 100%;\n    display: grid;\n    grid-template-columns: 5fr 1fr;\n    align-items: center;\n    justify-items: center;\n}\n\n#ship-to-add {\n    border: 1px solid black;\n}\n\n.highlight {\n    border: 2px solid yel